/* 기본 스타일 설정 */
body {
    margin: 0; /* 기본 margin 제거 */
    padding: 0; /* 기본 padding 제거 */
    font-family: Arial, sans-serif; /* 폰트 설정 */
    display: flex; /* 플렉스 박스 레이아웃 사용 */
    flex-direction: column; /* 플렉스 아이템을 세로로 정렬 */
    height: 100vh; /* Viewport height를 100%로 설정 */
}

/* 비디오 컨테이너 스타일 */
.video-container {
    flex: 0 0 20%; /* 전체 높이의 60%를 차지 */
    position: relative; /* 자식 요소를 위한 상대적 위치 설정 */
    height: 30%;
}

/* 비디오 배경 스타일 */
.video-background {
    width: 100%; /* 가로 100% */
    height: 40%; /* 세로 100% */
    object-fit:none; /* 비디오를 컨테이너에 맞게 조정 */
}

/* 환영 메시지 스타일 (필요 시 삭제 가능) */
.welcome-message {
    position: absolute; /* 부모의 위치를 기준으로 절대적 위치 설정 */
    top: 30%; /* 상단에서 10% 위치 */
    left: 50%; /* 좌측에서 50% 위치 */
    transform: translate(-50%, -50%); /* 중앙 정렬 */
    color: white; /* 글자 색상 흰색 */
    text-align: center; /* 텍스트 가운데 정렬 */
    // z-index: 1; /* z-index 설정으로 다른 요소 위에 표시 */
    font-size: 50px;
}
