/* 헤더 스타일 */
header {
    background-color: white; /* 배경색 흰색 */
    padding: 10px 0; /* 상하단 여백 10px */
}

/* 네비게이션 스타일 */
header nav {
    display: flex; /* 플렉스 박스 레이아웃 사용 */
    justify-content: space-between; /* 공간을 균등하게 배분 */
    align-items: center; /* 아이템을 수직 가운데 정렬 */
}

/* 로고 스타일 */
header .logo img {
    width: 150px; /* 로고 너비 150px */
}

/* 네비게이션 메뉴 스타일 */
header ul {
    list-style-type: none; /* 리스트 스타일 제거 */
    display: flex; /* 플렉스 박스 레이아웃 사용 */
    justify-content: flex-end; /* 오른쪽 정렬 */
    align-items: center; /* 아이템을 수직 가운데 정렬 */
}

header ul li {
    margin: 0 10px; /* 좌우 여백 10px */
}

/* 네비게이션 버튼 스타일 */
header ul li button {
    padding: 10px 20px; /* 내부 여백 설정 */
    margin: 10px; /* 외부 여백 설정 */
    background-color: white; /* 기본 버튼 배경색 흰색 */
    border: none; /* 테두리 없음 */
    border-radius: 5px; /* 모서리 둥글게 */
    color: black; /* 글자 색상 검정 */
    cursor: pointer; /* 커서 포인터로 변경 */
    font-size: 16px; /* 글자 크기 16px */
}

header ul li button:hover {
    background-color: #e0e0e0; /* 호버 시 배경색 변경 */
}

/* 회원가입 및 로그인 버튼 스타일 */
header ul li button.black-button {
    background-color: black; /* 배경색 검정 */
    color: white; /* 글자 색상 흰색 */
}


/* 즐겨찾기 및 하단 섹션 스타일 (필요 시 삭제 가능) */
.favorites-section, .lower-sections {
    display: flex; /* 플렉스 박스 레이아웃 사용 */
    justify-content: space-around; /* 공간을 균등하게 배분 */
    margin-top: 20px; /* 상단 여백 20px */
}

/* 하단 섹션의 인기글과 뉴스 스타일 */
.popular-posts, .news-section {
    width: 45%; /* 너비 45% */
}
.beach-info-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px; /* 테이블 위 여백 설정 */
}

.beach-info-table th, .beach-info-table td {
  padding: 8px 12px; /* 셀 안의 여백 설정 */
  text-align: center; /* 셀 안의 텍스트 정렬 */
  border: 1px solid #ddd; /* 테이블 선 설정 */
}

.beach-info-table th {
  background-color: #f2f2f2; /* 헤더 배경색 설정 */
  font-weight: bold; /* 헤더 글꼴 굵게 설정 */
}
/* 전체 컨테이너 스타일 */
/* 전체 컨테이너 스타일 */
.spot-info-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); /* 두 개씩 배치되도록 설정 */
  gap: 20px; /* 요소들 사이의 간격 */
  margin: 0 auto; /* 가운데 정렬 */
  max-width: 680px; /* 전체 컨테이너의 최대 너비 */
}

/* 각 SpotInfo 컴포넌트 스타일 */
.spot-info {
  background-color: darkgrey;
  height: 260px;
  box-sizing: border-box; /* padding과 border가 width와 height에 포함됨 */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 5px;
}

.buttons{
  margin-left: 150px;
  margin-bottom: 80px;
}

/* 미디어 쿼리: 화면 너비가 768px 이하일 때 */
@media (max-width: 768px) {


    .favorites-section, .lower-sections {
        flex-direction: column; /* 플렉스 아이템을 세로로 정렬 */
        align-items: center; /* 아이템을 수직 가운데 정렬 */
    }

    .popular-posts, .news-section {
        width: 90%; /* 너비 90% */
        margin-bottom: 20px; /* 하단 여백 20px */
    }
}

/* 미디어 쿼리: 화면 너비가 480px 이하일 때 */
@media (max-width: 480px) {
   
    .favorites-section, .lower-sections {
        flex-direction: column; /* 플렉스 아이템을 세로로 정렬 */
        align-items: center; /* 아이템을 수직 가운데 정렬 */
    }

    .popular-posts, .news-section {
        width: 90%; /* 너비 90% */
    }
    
}


